@charset "UTF-8";

.menuBar{
    /* padding: 1rem; */
    background: #be5eff;
    width: 100%;
    margin-bottom: 1rem;
}
.tabs li{
    font-size: 1rem;
    font-weight: bold;
    display: inline-block;
    color: #efefef;
    padding: 1rem;
}
.tabs li.active{
    background-color: #6f03b7;
    height: 100%;
}


.dimmed_layer_wrapper .full_layer {
    overflow:hidden;
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0;
    width:100%;
    height:100%;
    z-index:100;
    text-align:center;
    white-space:nowrap;
    font-size:0;
    display:inline-block;
    vertical-align:middle;
    height:100%;
    content:'';
}
.common_alert{
    z-index:2;
    position:relative;
    display:inline-block;
    vertical-align:left;
    min-height: 500px;
    max-height:100%;
    width:300px;
    border:2px solid black;
    background-color:grey;
    white-space:normal;
    word-break:break-word;
    text-align:left;
    border-radius:10px;
    padding:30px;
    margin:auto;
    box-sizing:border-box;
    font-size: 16px;
}

.common_alert .header { 
	position:relative;
	font-size: 14px;
	background-color: #000;
	color: white;
	text-align: center;
}

.common_alert .chat { height: 320px; background:white; overflow:scroll; margin-bottom:20px}
.common_alert .chat ul { padding: 0; width: 100%; list-style: none; background-color:white; }
.common_alert .chat ul li { width: 100%; color:black; height:100%; margin-bottom:5px}
.common_alert .chat ul li.left { text-align: left; }
.common_alert .chat ul li.right { text-align: right; }

.common_alert .chat ul li > div { font-size: 9px;  }
.common_alert .chat ul li > div.sender { margin: 10px 20px 0 20px; font-weight: bold; }
.common_alert .chat ul li > div.message { display: inline-block; word-break:break-all; margin: 5px 20px; max-width: 65%; border: 1px solid #888; padding: 10px; border-radius: 5px; background-color: #FCFCFC; color: #555; text-align: left; }
 
.common_alert .input-div { position: relative; bottom: 0; width: 91%; text-align: center; border-top: 1px solid #F18C7E; }
.common_alert .input-div > textarea { width: 100%; height: 30px; border: none; padding: 10px; }
 
.format { display: none; }